'use strict';

/**
* Compute a moving product incrementally.
*
* @module @stdlib/stats-incr-mprod
*
* @example
* var incrmprod = require( '@stdlib/stats-incr-mprod' );
*
* var accumulator = incrmprod( 3 );
*
* var p = accumulator();
* // returns null
*
* p = accumulator( 2.0 );
* // returns 2.0
*
* p = accumulator( -5.0 );
* // returns -10.0
*
* p = accumulator( 3.0 );
* // returns -30.0
*
* p = accumulator( 5.0 );
* // returns -75.0
*
* p = accumulator();
* // returns -75.0
*/

// MODULES //

var main = require( './main.js' );


// EXPORTS //

module.exports = main;
